<template>
  <div class="col-full" :class="$style.centered">
    <h1 :class="$style.headingLarge">
      Not found
    </h1>
    <p :class="$style.textLarge">
      Ooops, we couldn't find what you are looking for. Why don't you
      <router-link :to="{name:'Home'}">
        go home instead?
      </router-link>
    </p>
  </div>

</template>

<script>
  export default {
    name: 'PageNotFound',
    created () {
      this.$emit('ready')
    }
  }
</script>

<style module>
  .headingLarge {
    font-size: 6rem;
  }
  .textLarge {
    font-size: 3.5rem;
    font-weight: 100;
  }
  .centered {
    text-align: center;
  }
</style>



