<template>
  <div class="thread-list">

    <h2 class="list-title">Threads</h2>

    <ThreadListItem
      :key="thread.id"
      v-for="thread in threads"
      :thread="thread"
    />

  </div>
</template>

<script>
  import ThreadListItem from './ThreadListItem'

  export default {
    name: 'ThreadList',
    components: {
      ThreadListItem
    },

    props: {
      threads: {
        required: true,
        type: Array
      }
    }
  }
</script>

<style scoped>

</style>



