<template>
  <div class="post-list">
    <PostListItem
      v-for="post in posts"
      :post="post"
      :key="post['.key']"
    />
  </div>
</template>

<script>
  import PostListItem from './PostListItem'

  export default {
    name: 'PostList',
    components: {
      PostListItem
    },
    props: {
      posts: {
        required: true,
        type: Array
      }
    }
  }
</script>

<style scoped>

</style>



